import { createRouter, createWebHistory } from 'vue-router'
const routes = [
  {
    path:'/',
    redirect:'login'
  },
  {
    path:'/login',
    name:'Login',
    component: () => import( '@/views/Login')
  },
  {
    path: '/Home',
    name: 'Home',
    component: () => import('@/components/Home.vue'),
    redirect:'/initial',
    children:[
            //通用
            {
              path: '/initial',
              name: 'initial',
              component: () => import( '@/views/initial.vue')
            },
            {
              path: '/ChangePassword',
              name: 'ChangePassword',
              component: () => import( '@/views/ChangePassword.vue')
            },

            //学生
            {
              path: '/studentpage',
              name: 'studentpage',
              component: () => import( '@/views/student/studentpage.vue')
            },
            {
              path: '/selectcourse',
              name: 'selectcourse',
              component: () => import( '@/views/student/selectcourse.vue')
            },
            {
              path: '/selectdown',
              name: 'selectdown',
              component: () => import( '@/views/student/selectdown.vue')
            }, 

            {
              path: '/schedule',
              name: 'schedule',
              component: () => import( '@/views/student/schedule.vue')
            }, 

            {
              path: '/markpage',
              name: 'markpage',
              component: () => import( '@/views/student/markpage.vue')
            },
            //团队测试
            // {
            //   path: '/backbook',
            //   name: 'backbook',
            //   component: () => import( '@/views/backbook.vue')
            // },
            // {
            //   path: '/borrow',
            //   name: 'borrow',
            //   component: () => import( '@/views/borrow.vue')
            // },
            // {
            //   path: '/checkbook',
            //   name: 'checkbook',
            //   component: () => import( '@/views/checkbook.vue')
            // },
            // {
            //   path: '/insertbook',
            //   name: 'insertbook',
            //   component: () => import( '@/views/insertbook.vue')
            // },
            ]
  },

  {
    path: '/Homet',
    name: 'Homet',
    component: () => import('@/components/Homet.vue'),
    redirect:'/initialt',
    children:[
            //通用
            {
              path: '/initialt',
              name: 'initialt',
              component: () => import( '@/views/initialt.vue')
            },
            {
              path: '/ChangePasswordt',
              name: 'ChangePasswordt',
              component: () => import( '@/views/ChangePasswordt.vue')
            },
            //老师
            {
              path: '/mycourse',
              name: 'mycourse',
              component: () => import( '@/views/teacher/mycourse.vue')
            },    
            {
              path: '/givemark',
              name: 'givemark',
              component: () => import( '@/views/teacher/givemark.vue')
            },
            //管理员
            {
              path: '/CourseTable',
              name: 'CourseTable',
              component: () => import( '@/views/teacher/CourseTable.vue')
            },
            {
              path: '/CourseEdit',
              name: 'CourseEdit',
              component: () => import('@/views/teacher/CourseEdit.vue')
            }  
            ]
  }

]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
export default router
